#top-nav {
	height: 60px;
	display: flex;
	flex-direction: column;
	width: 100%;
	align-items: center;
	position: relative;
}


#bottom-nav {
	left: 0;
	right: 0;
	top: auto;
	bottom: 0;
	border-bottom: none;
	display: none;
	position: fixed;
}

.nav-container {
	display: flex;
	align-items: center;
	flex-direction: row;
	height: 60px;
	justify-content: center;
	width: 100%;
	position: fixed;
	top: 0;
	border-bottom: 1px solid rgba(219, 219, 219);
	background-color: rgba(255, 255, 255);
	z-index: 3;
}

.nav-container-bottom {
	position: fixed;
	bottom: 0;
	border-top: 1px solid rgba(219, 219, 219);
	background-color: rgba(255, 255, 255);
	z-index: 3;
	display: flex;
	align-items: center;
	flex-direction: row;
	justify-content: center;
	width: 100%;
	height: 45px;
}

.nav-inner-container {
	max-width: 975px;
	padding: 0 20px;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	z-index: 10;
}

#logo {
	flex: 1 0 127px;
	font-family: "Dancing Script", cursive;
	font-size: 2em;
	height: 36px;
	display: flex;
	align-items: center;
}

#logo-span {
	cursor: pointer;
}

.border-for-avatar {
	border: 1px solid rgba(38, 38, 38);
	border-radius: 50%;
	height: 28px;
	position: absolute;
	left: calc(100% - 26px);
	width: 28px;
	display: none;
}

#search-div {
	border-radius: 8px;
	align-items: center;
	display: flex;
	flex: 0 1 auto;
	height: 36px;
	min-width: 125px;
	width: 268px;
	position: relative;
}

.search-input {
	background: rgba(239, 239, 239);
	font-size: 16px;
	padding: 3px 16px;
	color: rgba(38, 38, 38);
	z-index: 2;
	border-radius: 8px;
	height: 100%;
	width: 100%;
	line-height: 18px;
	font-family: helvetica, "Font Awesome 5 Free";
	font-weight: 100;
	outline: none;
}

#icon-div {
	flex: 1 0 127px;
	align-content: center;
	align-items: center;
	display: flex;
	flex-direction: row;
	justify-content: flex-end;
	position: relative;
	padding-left: 24px;
	white-space: nowrap;
}

.icon-inner-div {
	display: flex;
	align-items: center;
	cursor: pointer;
}

.inner-bottom {
	flex: 1 1 auto;
	justify-content: center;
	min-height: 0;
	min-width: 0;
	height: 100%;
}

.ava {
	height: 100%;
	width: 100%;
	border-radius: 50%;
}

.icon-inner-div:not(:first-child) {
	margin-left: 22px;
}

#search-arrow-div,
#notification-arrow-div,
#profile-arrow-div {
	display: none;
	position: absolute;
	width: 14px;
	height: 14px;
	top: 115%;
	left: 50%;
	background-color: rgba(255, 255, 255);
	border: 1px solid rgba(255, 255, 255);
	transform: rotate(45deg);
	/* box-shadow: 0 0 5px 1px rgb(0 0 0 / 10%); */
	box-shadow: -7px -8px 13px 0px rgb(0 0 0 / 10%);
	z-index: 9000000;
}

#notification-arrow-div {
	top: 125%;
	left: 24.5%;
	cursor: default;
}

#profile-arrow-div {
	left: 5px;
	top: 33px;
	cursor: default;
}

.svg-div {
	display: flex;
}

.icon-relative {
	position: relative;
}

#search-results-div,
#notification-div,
#profile-div {
	display: none;
	position: absolute;
	width: 375px;
	height: 362px;
	background-color: rgba(255, 255, 255);
	border-radius: 6px;
	box-shadow: 0 0 5px 1px rgb(0 0 0 / 10%);
	top: 134%;
	left: -53.5px;
	z-index: 1001;
	flex-direction: column;
	overflow-y: scroll;
	padding-top: 10px;
}

#notification-div {
	width: 500px;
	height: 362px;
	top: 150%;
	left: -420px;
	cursor: default;
}

#profile-drop-div {
	position: relative;
}

.profile-drop-icon {
	margin-right: 12px;
	display: flex;
	justify-content: flex-start;
	align-items: stretch;
}

.profile-drop-tex {
	display: flex;
	height: 28px;
	width: 170px;
	justify-content: flex-start;
	align-items: center;
	color: rgba(38, 38, 38);
	font-weight: 400;
	font-size: 14px;
	line-height: 18px;
	/* margin: -3px 0 -4px; */
}

#profile-div {
	cursor: default;
	top: 15px;
	width: 230px;
	height: 90px;
	top: 38px;
	left: -180px;
	padding-top: 0;
}

.profile-drop-down-single-div:last-child {
	border-top: 1px solid rgba(219, 219, 219);
	padding-left: 18px;
}

.profile-drop-down-single-div {
	padding-bottom: 8px;
	padding-top: 8px;
	padding-left: 16px;
	padding-right: 16px;
	justify-content: flex-start;
	align-items: center;
	display: flex;
	cursor: pointer;
}

.profile-drop-down-single-div:hover {
	background-color: rgba(250, 250, 250);
}

.noti-modal-single-result {
	cursor: pointer;
}

.noti-modal-single-result span:nth-child(2) {
	font-weight: bold;
}

.noti-date {
	color: #8e8e8e;
}

#no-notifications,
#no-notifications-modal {
	align-self: center;
	margin-top: auto;
	margin-bottom: auto;
	color: #8e8e8e;
}

#noti-amount, #mess-amount {
	position: absolute;
	justify-content: center;
	align-items: center;
	right: -8px;
	top: -6px;
	border-radius: 100px;
	width: 18px;
	height: 18px;
	background-color: #ed4956;
	color: rgba(255, 255, 255);
	font-size: 11px;
	padding: 0 3px;
	display: none;
}

#noti-amount-mobile {
	background: #ed4956;
	border-radius: 2px;
	bottom: 5px;
	height: 4px;
	left: 0;
	margin: 0 auto;
	position: absolute;
	right: 0;
	transform: translateX(-0.5px);
	width: 4px;
	display: none;
}

#mobile-noti-icon {
	position: relative;
}

#not-found,
#spinner,
#not-found-share-modal,
#spinner-share-modal,
#spinner-modal,
#not-found-modal, #spinner-tag {
	display: none;
	color: #8e8e8e;
	align-self: center;
	margin-top: auto;
	margin-bottom: auto;
}

#close-search-result-div,
#small-spinner,
#small-spinner-share-modal,
#close-search-result-div-modal,
#small-spinner-modal,
#close-tag-search-result,
#small-spinner-tag {
	color: #8e8e8e;
	position: absolute;
	right: 12px;
	z-index: 100000;
	font-size: 18px;
	cursor: pointer;
}

#close-tag-search-result,
#small-spinner-tag, #spinner-tag {
	display: none;
}

#small-spinner-share-modal {
	cursor: default;
	right: 27px;
}

#search-results-div .share-modal-single-result {
	cursor: pointer;
}

#search-results-div .share-modal-single-result:hover {
	background-color: #fafafa;
}

.noti-link {
	display: flex;
	align-items: center;
}

@media (max-width: 650px) {
	#search-div {
		display: none;
	}

	.icon-to-hide {
		display: none;
	}

	#top-nav {
		height: 45px;
	}

	#bottom-nav {
		display: flex;
		z-index: 10;
	}

	.nav-container {
		height: 45px;
	}

	.icon-inner-div:not(:first-child) {
		margin-left: 0px;
	}

	.nav-container-bottom {
		padding: 0px;
	}

	.border-for-avatar {
		left: 90%;
		top: 50%;
		-webkit-transform: translate(-50%, -50%);
		transform: translate(-50%, -50%);
		width: 28px;
	}
}
