#inbox-section {
	display: flex;
	max-width: 935px;
	flex-grow: 1;
	margin: 0 auto;
	position: relative;
	width: 100%;
	flex-shrink: 0;
	border: 1px solid #dbdbdb;
	border-radius: 4px;
	height: 570px;
	box-sizing: border-box;
	margin-top: 20px;
	margin-bottom: 20px;
	background-color: #ffffff;
    overflow-y: scroll;
}

#inbox-left {
	width: 350px;
	border-right: 1px solid #dbdbdb;
	flex-shrink: 0;
}

#inbox-right {
	max-height: 570px;
	width: 100%;
	display: flex;
    overflow-y: scroll;
}

#inbox-left-header {
	height: 60px;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	border-bottom: 1px solid #dbdbdb;
	font-weight: 600;
	font-size: 16px;
	position: relative;
}

#new-message-icon-span {
	position: absolute;
	right: 20px;
	cursor: pointer;
}

#inbox-message-list {
	width: 100%;
	max-height: 510px;
	display: flex;
	flex-direction: column;
	overflow-y: scroll;
	padding-top: 8px;
	flex: 1;
}

.conversation-div {
	padding: 8px 20px;
	display: flex;
	align-items: center;
	cursor: pointer;
}

.conversation-div:hover {
	background-color: #fafafa;
}

.messages-avatars {
	cursor: pointer;
}

.conversation-div img {
	margin-right: 12px;
	height: 56px;
	width: 56px;
	border-radius: 50%;
}

.conversation-div-info {
	display: flex;
	flex-direction: column;
	width: 100%;
	justify-content: center;
}

.conversation-div-message {
	overflow: hidden;
	max-height: 17px;
	color: #8e8e8e;
}

time {
	color: #8e8e8e;
}

#inbox-send-message-info {
	display: flex;
	flex-direction: column;
	margin: auto;
	gap: 10px;
	align-items: center;
}

#inbox-send-message-info span {
	font-weight: 300;
	font-size: 22px;
}

#inbox-send-message-info button {
	background-color: #0095f6;
	border-radius: 4px;
	color: white;
	font-size: 14px;
	font-weight: 600;
	height: 30px;
	width: 118px;
	cursor: pointer;
}

#inbox-right-conversation {
	display: none;
	flex-direction: column;
	width: 100%;
	height: 100%;
    overflow-y: scroll;
    
}

#inbox-right-conversation-area {
	height: 100%;
	padding: 20px;
	display: flex;
	flex-direction: column;
    overflow-y: scroll;
}

#inbox-right-conversation-header {
	min-height: 60px;
	max-height: 60px;
	border-bottom: 1px solid #dbdbdb;
	width: 100%;
	display: flex;
	align-items: center;
	padding-left: 28px;
	padding-right: 20px;
	position: relative;
}

#conversation-header-avatar {
	height: 24px;
	width: 24px;
	border-radius: 50%;
	cursor: pointer;
}

#conversation-header-username {
	margin-left: 12px;
	font-weight: 600;
	font-size: 16px;
	cursor: pointer;
}

#conversation-header-delete {
	position: absolute;
	right: 20px;
	cursor: pointer;
}

.conversation-row {
	width: 100%;
	display: flex;
	padding-bottom: 10px;
	gap: 8px;
}

.conversation-row img {
	width: 24px;
	height: 24px;
	border-radius: 50%;
	align-self: flex-end;
}

.conversation-cloud-right {
	display: inline-block;
	max-width: 235px;
	padding: 16px;
	min-height: 50px;
	border-radius: 22px;
	background-color: #efefef;
	overflow-wrap: break-word;
	margin-left: auto;
}

.conversation-cloud-left {
	display: inline-block;
	max-width: 235px;
	padding: 16px;
	min-height: 50px;
	border-radius: 22px;
	border: 1px solid #efefef;
	margin-right: auto;
	overflow-wrap: break-word;
}

#go-back-inbox-mobile {
	display: none;
}

#inbox-right-add-message {
    display: flex;
	width: 100%;
}

#add-message-section {
	width: 100%;
}



@media (max-width: 650px) {

    #top-section {
        min-height: -webkit-fill-available;
    }

    

    html, body {
        height: 100%;
    }

	#inbox-left {
		width: 100%;
		height: 100%;
	}

	#inbox-right {
		display: none;
		max-height: 100%;       
	}

	#inbox-section {
		margin: 0px;
		border: 0px;
	}

	#add-message-section {
		display: flex;
		min-height: 50px;
		max-height: 50px;
        width: 100%;
	}

	#go-back-inbox-mobile {
		display: flex;
		margin-right: 15px;
	}

	#inbox-right-conversation-header {
		padding-left: 20px;
	}

    #inbox-right-add-message {
        padding-bottom: 44px;
		
    }

   

}
